.restaurant {
	padding-bottom: 73px;
	&_karaoke {
		background-color: $black;
		.subtitle-md,
		.title-sm,
		.title-lg {
			color: $white; } }
	@include sm {
		padding-bottom: 27px; }
	&__article {
		margin-top: 73px;
		@include sm {
			margin-top: 27px; } }
	&__top {
		padding: 314px 0 38px;
		background-size: cover;
		background-position: 50% 50%;
		background-repeat: no-repeat; }
	&__main {
		padding-bottom: 75px;
		@include sm {
			padding-bottom: 27px; } }
	&__title-sm {
		text-align: center; }
	&__title-lg {
		text-align: center; } }
.banner {
	position: relative;
	overflow: hidden;
	margin-top: 73px;
	padding: 245px 0;
	text-align: center;
	@include sm {
		padding: 90px 0;
		margin-top: 27px; }
	&__in {
		position: relative;
		z-index: 1; }
	&__bg {
		position: absolute;
		top: -50px;
		left: 0;
		right: 0;
		bottom: -50px;
		background-size: cover;
		background-position: 50% 50%;
		background-repeat: no-repeat; } }
